.header {
	font-weight: 700;
	font-size: 20px;
	line-height: 22px;
	text-transform: uppercase;

	color: #000000;
}
.headerSale {
	font-weight: 700;
	font-size: 15px;
	line-height: 17px;

	color: #f99c11;
	padding: 5px;
	gap: 10px;

	width: 52px;
	height: 27px;

	background-color: #fff0d2;
	border-radius: 10px;
}
.headerWrapper {
	display: flex;
	column-gap: 10px;
	align-items: center;
	margin-bottom: 30px;
}
.list {
	position: relative;
	display: flex;
	column-gap: 30px;
	margin-bottom: 20px;
}
.leftBtn {
	z-index: 10001;
}
